<template>
  <div>
    <Loading v-if="loading" />
    <GmapMap
        :class="{ load: loading }"
        :center="{ lat: 49.294683, lng: 31.599877 }"
        :zoom="zoom"
        :map-id="options.mapId"
        :options="options"
        style="width: 100%; height: 100vh"
    >
      <div v-if="!loading">
        <div v-for="(proposal, index) in proposals" :key="index">
          <gmap-custom-marker
              :marker="{ lat: proposal.location.lat, lng: proposal.location.lng }"
              class="culture"
          >
            <div @click="showInfoCard(proposal)">
              <img v-if="proposal.soy !== '0' && proposal.soy"
                   src="/images/soy.png" alt="soy" />

              <img v-if="proposal.sunflower !== '0' && proposal.sunflower"
                   src="/images/sunflower.png" alt="sunflower" />
            </div>
          </gmap-custom-marker>
        </div>
      </div>
    </GmapMap>
    <InfoCard v-if="active" :proposal="active_proposal" @close-info-card="closeInfoCard" />
  </div>
</template>
 
<script>
import Loading from "@/components/Loading.vue";
import InfoCard from "@/components/InfoCard.vue";

const { GoogleSpreadsheet } = require('google-spreadsheet');
const creds = require('@/client_secret.json');
	export default {
		name: "Sheet",
    components: {InfoCard, Loading},
		props: ["sheet"],
    data() {
      return {
        zoom: 6,
        options: {
          disableDefaultUI: true,
          mapId: 'e05c23c1b569a6bf',
          gestureHandling: 'greedy',
          clickableIcons: false
        },
        proposals: [],
        active: false,
        active_proposal: {},
        loading: true
      }
    },
		methods:{
			async accessSpreadSheet() {
				const doc = new GoogleSpreadsheet('1noy-xQfOuuU267CpGjXIL22rtbBFv92ofUm30jzSt14');
				await doc.useServiceAccountAuth(creds);
				await doc.loadInfo(); 
				const sheet = doc.sheetsByIndex[0];
				await sheet.getRows().then((proposals) => {
          this.proposals = proposals;
          this.setPoints();
        });
			},
      setPoints() {
        this.proposals.forEach((value, index) => {
          this.placeSearch(value.address, index);
        });
      },
      showInfoCard(proposal) {
        console.log('click', proposal)
        this.active_proposal = proposal
        this.active = true
      },
      closeInfoCard() {
        console.log('close')
        this.active = false
      },
      async placeSearch(address, index) {
        const options = {
          url: 'http://192.168.31.112:8080/maps/api/place/textsearch/json?query='+ address +'&key=AIzaSyBX9VzP4EtgebB1iQw6L-eAbOLxdJR7yKI',
          method: 'GET',
          headers: {
            'Access-Control-Allow-Origin': '*',
            'Content-type': 'application/json',
          }
        }
        this.axios(options)
            .then((response) => {
              return this.proposals[index].location = response.data.results[0].geometry.location
            });
      },
		},
		created() {
			this.accessSpreadSheet();
      setTimeout(() => {this.loading = false;}, 3000)
		}
	}
</script>

<style scoped>
.load {
  filter: brightness(0.5);
}
.culture {
  cursor: pointer;
}

.culture img {
  max-width: 24px;
}
</style>